import TagInput from '~/components/TagInput'
export default {
  components: {
    TagInput
    // MarkdownHelp
  },
  props: {
    nodeId: {
      type: Number,
      default: 0
    },
    tagId: {
      type: Number,
      default: -1
    },
    imageList: {
      type: Array,
      required: false
    },
    onImageUploadCallBack: {
      type: Function,
      required: false
    },
    onTagChange: {
      type: Function,
      required: false
    }
  },
  data() {
    return {
      content: '',
      images: [],
      editTags: [],
      message: '',
      maxWordCount: 300,
      showUploader: false,
      maxImageCount: 3,
      activedIndex: this.tagId,
      activityTags: [
        { name: '最新', index: -1 },
        { name: '问答', index: 39 },
        { name: '冷知识', index: 40 },
        { name: '哲思', index: 34 },
        { name: '产品与运营', index: 41 },
        { name: '价值', index: 33 },
        { name: '每日幽默', index: 35 },
        { name: '旅行', index: 38 },
        { name: '美食', index: 36 },
        { name: '婚恋', index: 37 },
        { name: '成长', index: 32 }
      ],
      inputTagReset: true
    }
  },
  computed: {
    hasContent() {
      return this.content && this.content.length > 0
    },
    wordCount() {
      return this.content ? this.content.length : 0
    },
    imageCount() {
      return this.images ? this.images.length : 0
    },
    user() {
      return this.$store.state.user.current
    }
  },
  mounted() {
    // this.activedIndex = this.tagId
    if (this.imageList && this.imageList.length > 0) {
      this.images = this.imageList
    }
    // this.editTags = ['美食', '小说1']
    // console.log('activedIndex:' + this.activedIndex)
  },
  methods: {
    onInput() {},
    async doSubmit() {
      if (!this.user) {
        this.message = '发表失败，请登录后重试'
        return
      }
      if (!this.hasContent) {
        this.message = '发表失败，请输入内容'
        return
      }
      this.showUploader = false // 关闭图片上传框
      try {
        const ret = await this.$axios.post('/api/tweet/create', {
          content: this.content,
          imageList: JSON.stringify(this.images),
          tags: this.editTags ? this.editTags.join(',') : ''
        })
        this.content = ''
        this.message = ''
        this.$emit('created', ret)
      } catch (e) {
        this.message = e.message || e
        console.log('message: ' + JSON.stringify(e))
      }
    },
    suggestTags() {
      const tags = []
      this.activityTags.forEach((a) => {
        if (a.index !== -1) {
          tags.push(a.name)
        }
      })
      return tags
    },
    activitySelectClick(activity) {
      // console.log('activity:' + activity.name)
      this.activedIndex = activity.index
      this.editTags = [activity.name]

      this.inputTagReset = false
      setImmediate(() => {
        this.inputTagReset = true
      })

      if (this.onTagChange) {
        this.onTagChange(this.activedIndex)
      }
    },
    handleImageUploadClick() {
      this.$refs.imageInput.click()
    },
    handleParse(e) {
      const items = e.clipboardData && e.clipboardData.items
      let file = null
      if (items !== undefined && items && items.length) {
        for (let i = 0; i < items.length; i++) {
          if (items[i].type && items[i].type.includes('image')) {
            file = items[i].getAsFile()
          }
        }
      }

      if (!file) {
        return
      }

      this.showUploader = true // 展开上传面板
      e.preventDefault() // 阻止默认行为即不让剪贴板内容在div中显示出来

      if (this.imageCount + 1 > this.maxImageCount) {
        this.message = '图片数量超过上限'
        return
      }

      this.upload(file) // 上传
    },
    handleDrag(e) {
      e.stopPropagation()
      e.preventDefault()

      const files = []
      const items = e.dataTransfer.items
      if (items && items.length) {
        if (items && items.length) {
          for (let i = 0; i < items.length; i++) {
            if (items[i].type.includes('image')) {
              files.push(items[i].getAsFile())
            }
          }
        }
      }

      if (files && files.length) {
        this.showUploader = true // 展开上传面板
        this.uploadFiles(files)
      }
    },
    async handleImageUploadChange(ev) {
      const files = ev.target.files
      if (!files) return

      await this.uploadFiles(files)

      // 清理文件输入框
      this.$refs.imageInput.value = null
    },
    async uploadFiles(files) {
      if (files.length === 0) {
        return
      }

      if (this.imageCount + files.length > this.maxImageCount) {
        this.message = '图片数量超过上限'
        return
      }

      for (let i = 0; i < files.length; i++) {
        await this.upload(files[i])
      }
    },
    async upload(file) {
      try {
        const formData = new FormData()
        formData.append('image', file, file.name)
        const ret = await this.$axios.post('/api/upload', formData)
        this.images.push(ret.url)
      } catch (e) {
        this.$toast.error(e.message)
        this.message = e.message || e
      }
      if (this.onImageUploadCallBack) {
        this.onImageUploadCallBack(this.images)
      }
    },
    removeImg(img) {
      const index = this.images.indexOf(img)
      if (index !== -1) {
        this.images.splice(index, 1)
      }
      if (this.onImageUploadCallBack) {
        this.onImageUploadCallBack(this.images)
      }
    }
  }
}
