export default {
  props: {
    // 请求URL
    url: {
      type: String,
      required: true
    },
    // 请求参数
    params: {
      type: Object,
      default() {
        return {}
      }
    },
    // 初始化数据
    initData: {
      type: Object,
      default() {
        return {
          results: [],
          page: ''
        }
      }
    },
    tagId: {
      type: Number,
      required: false,
      default() {
        return -1
      }
    }
  },
  data() {
    return {
      page: 1, // 分页标识
      results: this.initData.results || [], // 列表数据
      hasMore: true, // 是否有更多数据
      loading: false // 是否正在加载中
    }
  },
  computed: {
    // 是否禁言自动加载
    disabled() {
      return this.loading || !this.hasMore
    }
  },
  mounted() {
    // console.log('more mounted')
    this.loadMore()
  },
  methods: {
    async loadMore() {
      // console.log('loadMore')
      this.loading = true
      try {
        const _params = Object.assign(this.params || {}, {
          page: this.page,
          tagId: this.tagId
        })
        const ret = await this.$axios.get(this.url, {
          params: _params
        })
        this.hasMore = this.checkNextPage(ret.page)
        if (ret.results && ret.results.length) {
          ret.results.forEach((item) => {
            this.results.push(item)
          })
        } else {
          this.hasMore = false
        }
      } catch (err) {
        this.hasMore = false
        console.error(err)
      } finally {
        this.loading = false
      }
    },
    /**
     * 在results最前面加一条数据
     */
    unshiftResults(item) {
      if (item) {
        this.results.unshift(item)
      }
    },
    /**
     * 在results最后面加一条数据
     */
    pushResults(item) {
      if (item) {
        this.results.push(item)
      }
    },
    checkNextPage(page) {
      if (page && page.total > page.limit) {
        if (page.total / page.limit >= this.page) {
          this.page++
          return true
        }
      }
      return false
    }
  }
}
