import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import SiteNotice from '~/components/SiteNotice'
// import ScoreRank from '~/components/ScoreRank'
// import FriendLinks from '~/components/FriendLinks'
import TweetsPoster from '~/components/TweetsPoster'
import TweetsList from '~/components/TweetsList'
import LoadMore from '~/components/LoadMore'

library.add(faAngleUp, faAngleDown)
export default {
  components: {
    SiteNotice,
    // ScoreRank,
    // FriendLinks,
    TweetsPoster,
    TweetsList,
    LoadMore,
    FontAwesomeIcon
  },
  async asyncData({ $axios, query }) {
    try {
      // console.log('query :' + JSON.stringify(query))
      // const [tweetsPage] = await Promise.all([
      //   $axios.get('/api/tweet/list', {
      //     params: {
      //       page: query.p || 1
      //     }
      //   })
      //   // $axios.get('/api/user/score/rank'),
      //   // $axios.get('/api/link/toplinks')
      // ])
      // return { tweetsPage }
      if (query.tagId) {
        const tagId = await parseInt(query.tagId)
        return { tagId }
      }

      return
    } catch (e) {
      console.error(e)
    }
  },
  data() {
    return {
      tagId: -1,
      loadMoreReset: true
    }
  },
  mounted() {},
  methods: {
    tweetsCreated(item) {
      this.$refs.tweetsLoadMore.unshiftResults(item)
    },
    onTagChange(tagId) {
      this.tagId = tagId

      this.loadMoreReset = false
      setImmediate(() => {
        this.loadMoreReset = true
      })
      const url = `${location.pathname}?${'tagId=' + tagId}`
      history.pushState({ url }, '', url)
    }
  },
  head() {
    return {
      title: this.$siteTitle('90社区—有趣动态'),
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$siteDescription()
        },
        { hid: 'keywords', name: 'keywords', content: this.$siteKeywords() }
      ]
    }
  }
}
